import prisma from "@/lib/prisma";
import bcrypt from "bcrypt";

export async function POST(req) {
  const { email, password } = await req.json();

  if (!email || !password) return new Response(JSON.stringify({ error: "Missing fields" }), { status: 400 });

  const user = await prisma.user.findUnique({ where: { email } });
  if (!user) return new Response(JSON.stringify({ error: "Invalid credentials" }), { status: 401 });

  const passwordMatch = await bcrypt.compare(password, user.password);
  if (!passwordMatch) return new Response(JSON.stringify({ error: "Invalid credentials" }), { status: 401 });

  return new Response(JSON.stringify({ user: { id: user.id, email: user.email, experienceLevel: user.experienceLevel } }), { status: 200 });
}
